import React from 'react';
import './DrawerToggleButton.css';
const DrawerToggleButton = ({ click }) => {
    const [square1, setsquare1] = React.useState(false)
    const [square2, setsquare2] = React.useState(false)
    const [square3, setsquare3] = React.useState(false)
    const [square4, setsquare4] = React.useState(false)

    return (

        <div>
            <div className="row">
                <div className="column" >
                    <i className="fa  fa-square fa-2x" aria-hidden="true" title="Home" onMouseEnter={() => { setsquare1(!square1) }} hidden={square1} style={{ color: "#fb3a1c" }}></i>
                    <div className="homebox" onMouseLeave={() => { setsquare1(!square1) }} hidden={!square1} >Home</div>

                </div>
                <div className="column" >
                    <i className="fa  fa-square fa-2x" aria-hidden="true" title="Services" onMouseEnter={() => { setsquare2(!square2) }} hidden={square2} style={{ color: "#fb3a1c" }}></i>
                    <div className="homeboxLeft" onMouseLeave={() => { setsquare2(!square2) }} hidden={!square2} >Services</div>

                </div>
            </div>

            <div className="row">
                <div className="column" >
                    <i className="fa  fa-square fa-2x" aria-hidden="true" title="Films" onMouseEnter={() => { setsquare3(!square3) }} hidden={square3} style={{ color: "#fb3a1c" }}></i>
                    <div className="homeboxdown" onMouseLeave={() => { setsquare3(!square3) }} hidden={!square3} >Films</div>


                </div>
                <div className="column" >
                    <i className="fa  fa-square fa-2x" aria-hidden="true" title="About Us" onMouseEnter={() => { setsquare4(!square4) }} hidden={square4} style={{ color: "#fb3a1c" }}></i>
                    <div className="homeboxLeft" onMouseLeave={() => { setsquare4(!square4) }} hidden={!square4} >AboutUs</div>

                </div>
            </div>
        </div>
    );

};

export default DrawerToggleButton;
